@import url(https://fonts.googleapis.com/css2?family=Sniglet&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Sniglet&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.peliculas{display:flex;flex-direction:column}

header{padding-top:20px;padding-bottom:20px;width:100%;color:white;background-color:#34626C;z-index:10;display:flex;justify-content:center}header .titulo{font-size:4em;font-weight:bold;font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif}

.main{width:100%;height:10em;color:white;display:flex;font-family:'Sniglet', cursive}.main .menu{width:100%;background-color:#839B97;align-items:center;display:flex;justify-content:space-around;padding:0.2em 0.8em;color:white}.main .menu input{height:1.6em;padding:0.8em;margin:0.8em;border-radius:0.4em;font-family:'Sniglet', cursive}.main .menu select{height:3.5em;margin:0.8em;border-radius:0.4em;font-family:'Sniglet', cursive}.main .menu a{all:unset;padding:0.8em;border-radius:0.4em;cursor:pointer;background-color:grey;transition:all 0.5s}.main .menu a:hover{background-color:#032e0d;color:white}

.pie{padding-top:15px;width:100%;height:2.5em;background-color:#c6b497;color:grey;text-align:center}

.movie{display:flex;flex-direction:column;padding:0.8em;margin:0.8em;align-items:center;justify-content:space-between;border-radius:0.3em;background-color:grey;font-family:'Sniglet', cursive}.movie img{width:80%}

.movies{display:grid;grid-template:repeat(1, 1fr)/repeat(4, 1fr);background-color:white}

